/**
 * 获取首页数据
 */
import axios from 'axios'

export function getHotSearches(){

    let url = "static/hot-search.json";

    return new Promise((success,error) => {
        axios.get(url)
        .then((res) => {
            success(res);
        })
        .catch((err) => {
            error(err);
        })
    });
    
}

